<template>
    <div class="QRCode">
        <h1>{{msg}}</h1>
        <a :href="msg">wxxz</a>
        <button id="butt" v-on:click="appSource">openApp</button>
    </div>
</template>

<script>
export default {
    name: "QRCode",
    data () {
        return {
           msg:this.appSource()
        }
    },
    mounted () {
        
        // window.location = this.appSource();
    },
    methods: {
        appSource () {
            var openIframe = createIframe();
            let ua = navigator.userAgent;
            let isWX = !!/MicroMessenger/i.test(ua);
            let isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            let isiPhone = ua.indexOf('iPhone') > -1 || ua.indexOf('Mac') > -1;
            var downloadUrl = "http://api.honpe.com:81/honpeapp/honpe.apk";
            if (isiOS || isiPhone) {
                downloadUrl = "https://itunes.apple.com/app/id1093743730";
            } 
            if (isWX) {
            //    downloadUrl = "微信浏览器";
               openIframe.src = downloadUrl;
            //    setTimeout(function() {
            //        window.location.href = "http://fir.im/jwxmerchant"; /* http://t.cn/RcxMVvL*/
            //    }, 500);
            }
            openIframe.src = downloadUrl;
            setTimeout(function() {
                   window.location.href = "http://fir.im/jwxmerchant"; /* http://t.cn/RcxMVvL*/
            }, 500);
            return downloadUrl;
        },
        createIframe () {
            var iframe;
            return function() {
                if(iframe) {
                    return iframe;
                } else {
                    iframe = document.createElement('iframe');
                    iframe.style.display = 'none';
                    document.body.appendChild(iframe);
                    return iframe;
                }
            }
        }
    }
}
</script>



